import React, {useEffect, useRef,useMemo} from 'react';
import useSVGCanvas from './useSVGCanvas.js';
import * as d3 from 'd3';

export default function WhiteHatStats(props){
    //this is a generic component for plotting a d3 plot
    const d3Container = useRef(null);
    //this automatically constructs an svg canvas the size of the parent container (height and width)
    //tTip automatically attaches a div of the class 'tooltip' if it doesn't already exist
    //this will automatically resize when the window changes so passing svg to a useeffect will re-trigger
    const [svg, height, width, tTip] = useSVGCanvas(d3Container);

    const yMargins = [20,10];
    const xMargins = [20,20];
    //what the x-axis is
    const sortkey = props.sortKey === undefined? 'age':props.sortKey;


    //draws a histogram colored by gender for each person
    useEffect(()=>{
        if(svg === undefined | props.data === undefined){ return }
        let plotData = props.data.victims.map(i=>i);

        //filter data based on selections passed from the map
        if(props.selectedStuff !== undefined){
            plotData = plotData.filter(d=> props.selectedStuff.ids.indexOf(d.victimID) > 0);
        }
        else if(props.zoomedState !== undefined){
            console.log('zoomed state',props.zoomedState)
            const state = props.data.states.filter(d=>d.state===props.zoomedState);
            plotData = plotData.filter(d=> state [0].ids.indexOf(d.victimID) >= 0);
        }

        //does javascript use stable sort?
        plotData.sort((a,b) => a.day - b.day).sort((a,b) => parseInt(a[sortkey]) - parseInt(b[sortkey]));
        
        //this gives all the entries of the values used on the x axis eg age or time. not unique
        var keyVals = plotData.map(d=>d[sortkey]);

        keyVals.sort((a,b)=> a-b);

        //get the data for the rectanges representing each person
        let markerData = [];
        let maxStack = 0;
        var finishedTimes = {}
        var currX = 0 ;
        let textData = [];
        let ii= 0;
        var newCountData = {'Men':0,'Women': 0,'Children': 0,'Teens': 0, 'Adults': 0}
        for(let key of keyVals){
            //we make sure we don't do any values twice
            if(finishedTimes[key] !== undefined){continue}
            finishedTimes[key] = true;

            //filter out all points at the givens age/date etc
            let points = plotData.filter(d=>parseInt(d[sortkey]) === parseInt(key));
            if(points.length > maxStack){
                maxStack = points.length;
            }
            if(points.length < 1){
                continue
            }

            //make an entry for each point at the given x value so we can
            //pass it back to the title in the top level app.js
            for(let i in points){
                let entry = Object.assign({},points[i]);
                entry.yPosition = parseInt(i);
                entry.xPosition = currX;
                markerData.push(entry);

                //keep track
                if(entry.gender === 'M'){
                    newCountData['Men'] += 1
                } else{
                    newCountData['Women'] += 1
                }
                if(parseInt(entry.age) < 10){
                    newCountData['Children'] += 1;
                } else if(parseInt(entry.age) < 20){
                    newCountData['Teens'] += 1;
                } else{
                    newCountData['Adults'] += 1;
                }
            }
            //do ticks every 50 if timestamps, every 2 for age
            if((sortkey !== 'timestamp') & (ii > 0) | (ii > 50)){
                let vkey = sortkey === 'timestamp'? 'date': sortkey;
                let textEntry = {
                    xPosition: currX,
                    yPosition: 0,
                    value: points[0][vkey],
                }
                textData.push(textEntry);
                ii = -1;
            }
            currX += 1;
            ii += 1;
        }

        //pass counts of each group back to app.js for the title
        props.setDeathCounts(newCountData);

        //figure out the correct size of the rectangles
        var maxWidth = (width-xMargins[0]-xMargins[1])/(currX-1);
        var maxHeight = (height - yMargins[0] - yMargins[0])/(maxStack+1);
        const yScale = d3.scaleLinear()
            .domain([0,maxStack+1])
            .range([yMargins[0]+2,height-yMargins[1]]);
        const xScale = d3.scaleLinear()
            .domain([0,currX])
            .range([xMargins[0],width-xMargins[1]-maxWidth])

        const getFill = (d) => d.gender === 'M'? 'Navy':"Magenta";
        svg.selectAll('.markers').remove();
        svg.selectAll('.markers').data(markerData)
            .enter().append('rect')
            .attr('class','markers')
            .attr('width',maxWidth)
            .attr('height',maxHeight)
            .attr('transform',d=>'translate(' + (xScale(d.xPosition)) + ',' + (yScale(d.yPosition)) + ')')
            .attr('fill',getFill)
            .attr('stroke','white')
            .attr('stroke-width',1)
            .attr('cursor','pointer')
            .on('mouseover',(e,d)=>{
                var tString = d.name === ''? 'unknown': d.name;
                tString += " (" + d.gender + ', ' + d.age + ')' + '</br>'
                    +"city: " + d.city + '</br>'
                    +'killed on ' + d.date + '</br>'
                    + 'url: ' + d.url;
                tTip.html(tString)
            }).on('mousemove',(e)=>{
                props.ToolTip.moveTTipEvent(tTip,e);
            })
            .on('mouseout',(e,d)=>{
                props.ToolTip.hideTTip(tTip);
            }).on('dblclick',(e,d)=>{
                if(d.url !== '' & d.url !== undefined){
                    window.open(d.url);
                }
            });

        svg.selectAll('.tickLabels').remove();
        svg.selectAll('.tickLabels').data(textData)
            .enter().append('text')
            .attr('class','tickLabels')
            .attr('x',d=>xScale(d.xPosition+.1))
            .attr('y',yMargins[0])
            .attr('font-size',Math.min(Math.max(maxWidth/1.5,12),yMargins[0]))
            .text(d=>d.value);

    },[props.data,svg,props.zoomedState,props.selectedStuff,sortkey]);
    return (
        <div
            className={"d3-component"}
            style={{'height':'99%','width':'99%'}}
            ref={d3Container}
        ></div>
    );
}